#root, html, body {
  height: 100%;
  margin: 0;
}
#wrapper {
  min-height: 100%;
  margin-bottom: -100px;
}
#push, footer {
  height: 100px;
}

header, footer {
  background-color: #EE6E73;
  color: #fff;
}

h1, footer p {
  margin: 0;
  padding: 15px;
  text-align: center;
}

.collection {
  max-width: 960px;
  margin: 5px auto;
}

.female {
  background-color: #ffebee !important;
}




